import React, { Component } from 'react';
import { connect } from 'react-redux';
import { useHistory } from 'react-router-dom'
import { CircularProgress } from '@material-ui/core';

// actions
import ProductsActions from '../redux/redux-sauce/products';
import ShopsActions from '../redux/redux-sauce/shops';


export function CatButtonComp (props) {
  let history = useHistory()
  const { category } = props

  function handleClick() {
    history.push("/category/" + category.name);
    props.getShops({category: category.name})
  }

  return (
    <div 
      key={category.id}
      className='card-style mx-0 my-1'
      onClick={handleClick}
      style={{
        'background': 'white',
        'width': '125px',
        'height': '100px',
        'color': 'white',
        'font-weight': 'bold',
        'font-size': '1.5em',
        'text-align': 'center',
        'word-break': 'break-all',
        'border-radius': '15px'
      }}
    >
      <p className='my-0 color-mainDark font-15'>
        {category.ru_name}
      </p>
      <img 
        src={'http://35.222.169.29' + category.images[0].catalog_image} />
    </div>
  )
}

class MainCats extends Component {
  componentDidMount() {
    this.props.getCategories()
  }

  render() {
    const { categories, listFetching } = this.props.state.products
    
    return (
      listFetching
        ? <CircularProgress className='mx-5 my-5 color-black align-middle text-center'/>
        : <div 
            className='' 
            style={{
              'display': 'flex',
              'flex-flow': 'row wrap',
              'justify-content': 'space-around'
            }}
          >
          {categories && categories.map(category =>
            <div 
              key={category.id}
              className='card-style mx-0 my-1'
              // onClick={}
              style={{
                'background': 'white',
                'width': '125px',
                'height': '100px',
                'color': 'white',
                'font-weight': 'bold',
                'font-size': '1.5em',
                'text-align': 'center',
                'word-break': 'break-all',
                'border-radius': '15px'
              }}
            >
              <CatButtonComp category={category} getShops={this.props.getShops}/>
            </div>
            )}
        </div>
    );
  }
}

const mapStateToProps = (state) => ({
  state: state
})

const mapDispatchToProps = (dispatch) => ({
  getCategories: query => dispatch(ProductsActions.getCategoriesRequest(query)),
  getShops: query => dispatch(ShopsActions.getShopsRequest(query)),
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(MainCats);
